*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  text-align: center;
}

.header{
  margin: 0;

  text-align: left;
  padding-left: 1rem;
  min-height: 3rem;
  background-color: black;
  color: white;
}

ul{
  min-height: 4rem;
  border-bottom: 1px solid black;
  padding : 0.5rem 1rem;
  list-style: none;

  display: flex;
  flex-direction: column;
  gap : 0.5rem;

  justify-content: space-between;
  align-items: flex-start;
}

ul :nth-child(1){
  font-size: 1.2rem;
  font-weight: 900;
}

.modal{
  margin-top: 20px;
  padding: 20px;
  background-color: #eeeeee;
  text-align: left;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
